<template>
  <div class="search" @scroll="scroll">
    <!-- 搜索条 -->
    <section class="f-input" action="#" method="get">
      <div class="inputcover">
        <i class="u-svg"> </i>
        <input
          type="text"
          placeholder="搜索歌曲、歌手、专辑"
          v-model.trim="value"
          @keyup.enter="value && (searching = true)"
          @focus="inputing = true"
          @blur="inputing = false"
        />
      </div>
    </section>
    <!-- 热门搜索 -->
    <section class="hots" v-if="!value && !searching">
      <h5>热门搜索</h5>
      <ul>
        <li
          v-for="hot in hots"
          :key="hot.first"
          @click="
            searching = true;
            value = hot.first;
          "
        >
          {{ hot.first }}
        </li>
      </ul>
      <ol class="history">
        <li
          v-for="(h, index) in history"
          :key="h"
          @click.stop="
            searching = true;
            value = h;
          "
        >
          <i class="svg-histy"></i>
          <div class="histyr">
            <span ref="span" :id="index"> {{ h }}</span>
            <figure class="close" @click.stop="historyMove">
              <i class="svg-close" :id="index"></i>
            </figure>
          </div>
        </li>
      </ol>
    </section>
    <!-- 搜索建议 -->
    <section class="suggests" v-if="value && !searching">
      <h3 class="title">搜索“{{ value }}”</h3>
      <ul>
        <li
          v-for="(item, index) in suggests"
          :key="index"
          @click="
            searching = true;
            value = item.keyword;
          "
        >
          <i class="svg-search"></i>
          <span>{{ item.keyword }}</span>
        </li>
      </ul>
    </section>

    <!-- 搜索结果 -->
    <section class="searchResults" v-if="searching">
      <ul>
        <SongListItem
          v-for="(item, index) in searchResults"
          :key="index"
          :item="item"
          @change-current-song="
            $emit('change-current-song', $event);
            $emit('change-current-play-list', searchResults);
          "
          :currentSongId="currentSongId"
          :playing="playing"
        ></SongListItem>
      </ul>
      <p v-if="!hasMore">没有更多了</p>
    </section>
  </div>
</template>

<script>
// import _ from "lodash";
import SongListItem from "@/components/SongListItem.vue";

export default {
  props: {
    currentSongId: {
      type: Number,
    },
    playing: Boolean,
  },
  components: { SongListItem },
  data() {
    return {
      hots: [],
      suggests: [],
      searchResults: [],
      value: "",
      searching: false,
      inputing: false,
      page: 0,
      hasMore: false,
      history: JSON.parse(window.localStorage.getItem("history")) || [],
      historymoves: [],
    };
  },
  methods: {
    // 删除历史
    historyMove(e) {
      this.historymoves = [];
      this.$refs.span.forEach((v) => {
        if (e.target.id === v.id) {
          if (!this.history[1]) {
            window.localStorage.setItem("history", JSON.stringify(""));
          }
        } else {
          this.historymoves = [...new Set([...this.historymoves, v.innerText])];
          window.localStorage.setItem("history", JSON.stringify(this.historymoves));
        }
      });
      this.history = JSON.parse(window.localStorage.getItem("history"));
    },
    // 触底
    scroll(event) {
      if (this.hasMore) {
        if (event.target.offsetHeight + event.target.scrollTop === event.target.scrollHeight) {
          console.log("触底");

          this.getSearch();
        }
      } else {
        console.log("没有更多了");
      }
    },
    // 发送请求
    getSearch() {
      this.axios
        .get("https://apis.netstart.cn/music/search", {
          params: {
            keywords: this.value,
            limit: 30,
            offset: this.page * 30,
          },
        })
        .then((res) => {
          this.searchResults.push(...res.data.result.songs);
          this.page++;
          this.hasMore = res.data.result.hasMore;
        });

      // 记录一下
      // this.history.push(this.value)
      this.history = [...new Set([...this.history, this.value])];
      window.localStorage.setItem("history", JSON.stringify(this.history));
    },
  },
  created() {
    this.axios.get("https://apis.netstart.cn/music/search/hot").then((res) => {
      this.hots = res.data.result.hots;
    });
  },
  watch: {
    value(n) {
      if (this.inputing) {
        this.searching = false;
      }
      if (n && !this.searching) {
        this.axios
          .get("https://apis.netstart.cn/music/search/suggest", {
            params: {
              keywords: n,
              type: "mobile",
            },
          })
          .then((res) => {
            this.suggests = res.data.result.allMatch;
          });
      } else {
        this.suggests = [];
      }
    },

    searching(n) {
      if (n && this.value) {
        this.getSearch();
      } else {
        this.searchResults = [];
      }
    },
  },
};
</script>

