<template>
  <div class="home">
    <HomeTitle>编辑推荐</HomeTitle>

    <ul class="home-cardlist">
      <transition
        v-for="item in personalizeds"
        :key="item.id"
        name="custom-classes-transition"
        enter-active-class="animate__animated animate__fadeIn"
        leave-active-class="animate__animated animate__fadeOut"
      >
        <CardListItem :item="item" @toggle-show-play-card="showPlayCard = $event" />
      </transition>
    </ul>

    <HomeTitle>最新音乐</HomeTitle>
    <ul class="home-songlist">
      <SongListItem
        v-for="item in newsongs"
        :key="item.id"
        :item="item"
        @change-current-song="
          $emit('change-current-song', $event);
          $emit('change-current-play-list', newsongs);
        "
        :currentSongId="currentSongId"
        :playing="playing"
      ></SongListItem>
    </ul>
  </div>
</template>

<script>
import HomeTitle from "@/components/HomeTitle.vue";
import CardListItem from "@/components/CardListItem.vue";
import SongListItem from "@/components/SongListItem.vue";

export default {
  name: "Home",
  components: {
    HomeTitle,
    CardListItem,
    SongListItem,
  },
  props: {
    currentSongId: {
      type: Number,
    },
    playing: Boolean,
  },
  data() {
    return {
      personalizeds: [],
      newsongs: [],
      showPlayCard: false,
    };
  },

  created() {
    this.axios.get("https://apis.netstart.cn/music/personalized?limit=6").then((res) => {
      this.personalizeds = res.data.result;
    });

    this.axios.get("https://apis.netstart.cn/music/personalized/newsong").then((res) => {
      this.newsongs = res.data.result;
    });
  },
};
</script>

<style lang="less" scoped>
.home-cardlist {
  display: flex;
  flex-wrap: wrap;
}
</style>
