<template>
  <section class="playbar" :class="{ playing: playing }" @click="$emit('toggle-show-play-page', true)">
    <img
      v-if="!currentSong.album"
      :src="
        `${
          currentSong.song ? currentSong.picUrl : currentSong.al.picUrl
        }?imageView=1&type=webp&thumbnail=60x0`
      "
    />
    <img v-else :src="`${currentSong.artists[0].img1v1Url}?imageView=1&type=webp&thumbnail=60x0`" />
    <h3>
      {{ currentSong.name }}
      <span v-if="!currentSong.album">
        -
        {{ currentSong.song ? currentSong.song.artists[0].name : currentSong.ar[0].name }}
      </span>
      <span v-else>
        -
        {{ currentSong.artists[0].name }}
      </span>
    </h3>
    <div class="playingbf" @click.stop="$emit('toggle-playing-state')">
      <canvas width="40" height="40" ref="canvas"></canvas>
      <svg
        v-if="!playing"
        t="1629873417215"
        class="icon a"
        viewBox="0 0 1024 1024"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        p-id="2120"
        width="18"
        height="18"
      >
        <path
          d="M803.84 492.032 213.056 135.04C195.392 124.288 174.208 124.288 156.352 134.976 138.624 145.728 128 164.928 128 186.368l0 714.176c0 21.44 10.624 40.704 28.352 51.392C165.248 957.312 174.976 960 184.704 960c9.728 0 19.52-2.688 28.352-8.064l590.72-356.992c17.728-10.752 28.288-30.016 28.288-51.456C832.128 521.984 821.568 502.72 803.84 492.032zM189.568 891.712 187.136 193.792l578.624 349.696L189.568 891.712z"
          p-id="2121"
        ></path>
      </svg>
      <svg
        v-if="playing"
        t="1629873665092"
        class="icon"
        viewBox="0 0 1024 1024"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        p-id="4075"
        width="18"
        height="18"
      >
        <path
          d="M304 176h80v672h-80z m408 0h-64c-4.4 0-8 3.6-8 8v656c0 4.4 3.6 8 8 8h64c4.4 0 8-3.6 8-8V184c0-4.4-3.6-8-8-8z"
          p-id="4076"
        ></path>
      </svg>
    </div>
    <div class="list" @click.stop="$emit('toggle-show-play-list', true)">
      <svg
        t="1629946146446"
        class="icon"
        viewBox="0 0 1024 1024"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        p-id="6419"
        width="25"
        height="25"
      >
        <path
          d="M988.3 488.2c-85.3 0-154.8-69.4-154.8-154.8 0-19.7-16-35.7-35.7-35.7-19.7 0-35.7 16-35.7 35.7v233.3c-37.9-34-88-54.7-142.9-54.7-118.2 0-214.3 96.1-214.3 214.3S501 940.7 619.2 940.7c118.2 0 214.3-96.1 214.3-214.3 0-8.6-0.5-17.1-1.5-25.4 1-3.3 1.5-6.7 1.5-10.3V498.2c40.5 38.1 95 61.4 154.8 61.4 19.7 0 35.7-16 35.7-35.7 0-19.7-16-35.7-35.7-35.7z m-369.1 381c-78.8 0-142.9-64.1-142.9-142.9s64.1-142.9 142.9-142.9S762 647.5 762 726.3s-64 142.9-142.8 142.9zM35.7 154.8h762c19.7 0 35.7-16 35.7-35.7s-16-35.7-35.7-35.7h-762C16 83.3 0 99.3 0 119.1s16 35.7 35.7 35.7zM35.7 369.1h619.2c19.7 0 35.7-16 35.7-35.7s-16-35.7-35.7-35.7H35.7C16 297.7 0 313.7 0 333.4s16 35.7 35.7 35.7zM297.7 512h-262C16 512 0 528 0 547.7c0 19.7 16 35.7 35.7 35.7h262c19.7 0 35.7-16 35.7-35.7 0-19.7-16-35.7-35.7-35.7zM297.7 726.3h-262C16 726.3 0 742.3 0 762s16 35.7 35.7 35.7h262c19.7 0 35.7-16 35.7-35.7s-16-35.7-35.7-35.7z"
          fill="#8a8a8a"
          p-id="6420"
        ></path>
      </svg>
    </div>
  </section>
</template>

<script>
export default {
  props: {
    currentSong: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    currentPlayList: Array,
  },
  computed: {
    percentage: function() {
      return this.currentTime / this.duration;
    },
  },
  watch: {
    percentage: function(n) {
      var context = this.$refs.canvas.getContext("2d");
      context.clearRect(0, 0, 40, 40);
      context.beginPath();
      context.arc(20, 20, 18, (Math.PI / 180) * (0 - 90), (Math.PI / 180) * (360 * n - 90));
      context.strokeStyle = "#d43c33";
      context.lineWidth = 1;
      context.stroke();
      context.beginPath();
      context.arc(20, 20, 18, (Math.PI / 180) * (360 * n - 90), (Math.PI / 180) * (360 - 90));
      context.strokeStyle = "gray";
      context.lineWidth = 1;
      context.stroke();
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.playbar {
  text-align: left;
  width: 100%;
  height: 40px;
  background: white;
  position: fixed;
  bottom: 0;
  left: 0;

  display: flex;
  align-items: center;
  padding: 0 12px;

  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.15);

  img {
    width: 36px;
    height: 36px;
    border-radius: 50%;

    animation: rotate 8s linear infinite;
    animation-play-state: paused;
  }
  h3 {
    padding: 0 10px;
    flex: 1;
    font-size: 14px;

    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    span {
      color: rgb(151, 151, 151);
      font-size: 10px;
    }
  }
  .playingbf {
    width: 30px;
    height: 30px;
    margin-right: 10px;

    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    canvas {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
    }
    .icon.a {
      transform: translateX(2px);
    }
  }
  .list {
    .icon {
      margin-top: 2px;
    }
  }

  &.playing {
    img {
      animation-play-state: running;
    }
  }
}
</style>
