<script>
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
  },
  filters: {
    timeTier(time) {
      var da = new Date(time);
      var year = da.getFullYear() + "年";
      var month = da.getMonth() + 1 + "月";
      var date = da.getDate() + "日";
      return [year, month, date].join("");
    },
  },
};
</script>

