<template>
  <li @click="$router.push('/playlist?id=' + item.id)" class="card-item">
    <div class="thumb">
      <img
        class="bg"
        :src="`${item.picUrl}?imageView=1&type=webp&thumbnail=246x0`"
      />
      <span
        ><img
          style="width: 11px"
          src="data:image/svg+xml;base64,"
          alt=""
        />
        {{ item.playCount | parseCount }}</span
      >
    </div>
    <p>{{ item.name }}</p>
  </li>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
  },

  filters: {
    parseCount: function (value) {
      if (value > 100000000) {
        return (value / 100000000).toFixed(1) + "亿";
      } else if (value > 10000) {
        return (value / 10000).toFixed(1) + "万";
      } else {
        return value;
      }
    },
  },
};
</script>

<style lang="less">
.card-item {
  flex-basis: 25%;
  flex-grow: 1;

  &:nth-child(3n + 2) {
    margin: 0 3px;
  }

  .thumb {
    position: relative;
    top: 0;
    left: 0;
    img.bg {
      vertical-align: middle;
      width: 100%;
    }
    span {
      position: absolute;
      top: 3px;
      right: 3px;
      font-size: 10px;
      color: white;
    }
  }
  p {
    font-size: 13px;
    margin: 5px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
</style>
