<template>
  <div class="plhrader" v-if="item">
    <div
      class="mask"
      :style="{
        backgroundImage: 'url(' + item.coverImgUrl + '?imageView=1&type=webp&thumbnail=240x0',
      }"
    ></div>
    <!-- <i class="play-icon" @click="$emit('toggle-show-play-card', false)">
      <svg
        t="1630117054624"
        class="icon"
        viewBox="0 0 1024 1024"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        p-id="3149"
        width="22"
        height="22"
      >
        <path
          d="M960 306.1c0-9.3-3.6-18.5-10.8-25.7a36.576 36.576 0 0 0-51.9 0L511.7 666l-385-385.5a36.576 36.576 0 0 0-51.9 0 36.576 36.576 0 0 0 0 51.9L486 743.6c14.4 14.4 37.5 14.4 51.9 0l411.2-411.2c7.3-7.8 10.9-17 10.9-26.3z"
          p-id="3150"
          fill="#cdcdcd"
        ></path>
      </svg>
    </i> -->
    <div class="left">
      <img class="bg" :src="`${item.coverImgUrl}?imageView=1&type=webp&thumbnail=246x0`" />
      <span>
        <img
          style="width: 11px"
          src="data:image/svg+xml;base64,"
        />
        {{ item.playCount | parseCount }}
      </span>
    </div>
    <div class="right">
      <p class="name">{{ item.name }}</p>
      <span>
        <div>
          <img :src="item.creator.avatarUrl" />
        </div>
        <p>{{ item.creator.nickname | textNickname }}</p>
      </span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
  },
  filters: {
    parseCount: function(value) {
      if (value > 100000000) {
        return (value / 100000000).toFixed(1) + "亿";
      } else if (value > 10000) {
        return (value / 10000).toFixed(1) + "万";
      } else {
        return value;
      }
    },
    textNickname(txt) {
      if (txt.split("")[7]) {
        return (
          txt
            .split("")
            .splice(0, [7])
            .join("") + "..."
        );
      } else {
        return txt;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.plhrader {
  display: flex;
  background-color: rgba(0, 0, 0, 0.55);

  .mask {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    filter: blur(20px);
    z-index: -1;
    width: 100vw;
    height: 175px;
    position: absolute;
    left: 0;
    top: 0;
    background-size: cover;
    background-position: center;
  }
  .play-icon {
    position: absolute;
    top: 0;
    right: 0;
    width: 22px;
    height: 22px;
    margin-right: 12px;
  }
  .left {
    width: 126px;
    height: 126px;
    margin: 25px 12px;
    position: relative;
    img.bg {
      vertical-align: middle;
      width: 100%;
    }
    &::before {
      padding-left: 5px;
      line-height: 1.5;
      content: "歌单";
      font-size: 12px;
      color: white;
      position: absolute;
      top: 10px;
      left: 0;
      background: #af2829;
    }
    &::after {
      width: 1px;
      height: 1px;
      border: 9px solid #af2829;
      border-left-color: transparent;
      border-bottom-color: transparent;
      border-radius: 50%;
      content: "";
      position: absolute;
      top: 10px;
      left: 19px;
      transform: rotate(45deg);
    }
    span {
      font-size: 13px;
      color: white;
      position: absolute;
      top: 0;
      right: 0;
    }
  }
  .right {
    margin-top: 25px;
    .name {
      text-align: left;
      padding-top: 1px;
      font-size: 17px;
      line-height: 1.3;
      color: #fefefe;
      height: 44px;
      display: -webkit-box;
      -webkit-box-pack: center;
    }
    span {
      margin-top: 5px;

      display: flex;
      align-items: center;
      div {
        width: 30px;
        height: 30px;
        border-radius: 50%;
        overflow: hidden;
        img {
          width: 100%;
          height: auto;
        }
      }
      p {
        margin-left: 5px;
        font-size: 14px;
        color: hsla(0, 0%, 100%, 0.7);
      }
    }
  }
}
</style>
