<template>
  <div class="detail" v-if="detail">
    <!-- 头部 -->
    <PlayListHeader :item="detail" @toggle-show-play-card="showPlayCard = $event"></PlayListHeader>
    <PlayListSubtitle>歌曲列表</PlayListSubtitle>
    <!-- 歌曲详情 -->
    <ul>
      <SongListItem
        v-for="(item, index) in detail.tracks"
        :key="item.id"
        :item="item"
        @change-current-song="
          $emit('change-current-song', item);
          $emit('change-current-play-list', detail.tracks);
        "
        :currentSongId="currentSongId"
        :playing="playing"
        >{{ index + 1 }}</SongListItem
      >
      <!-- :class="{ lt3: index < 3 }" -->
    </ul>
    <!-- 精彩评论 -->
    <PlayListSubtitle v-if="comment.more">精彩评论</PlayListSubtitle>
    <ul>
      <WonderfulComment v-for="item in comment.hotComments" :key="item.id" :item="item"></WonderfulComment>
    </ul>
    <!-- 最新评论 -->
    <PlayListSubtitle>最新评论</PlayListSubtitle>
    <ul>
      <WonderfulComment v-for="item in comment.comments" :key="item.id" :item="item"></WonderfulComment>
    </ul>
  </div>
</template>

<script>
import SongListItem from "@/components/SongListItem.vue";
import PlayListHeader from "@/components/PlayListHeader.vue";
import PlayListSubtitle from "@/components/PlayListSubtitle.vue";
import WonderfulComment from "@/components/WonderfulComment.vue";
export default {
  components: {
    SongListItem,
    PlayListHeader,
    WonderfulComment,
    PlayListSubtitle,
  },
  props: {
    currentSongId: {
      type: Number,
    },
    playing: Boolean,
  },
  data() {
    return { detail: null, comment: [], showPlayCard: false, id: "" };
  },
  computed: {
    // a() {
    //   if (this.comment.hotComments[0] === null) {
    //     return true;
    //   }
    // },
  },
  methods: {
    getPlayListDetail(id) {
      this.axios
        .get("https://apis.netstart.cn/music/playlist/detail", {
          params: {
            id,
          },
        })
        .then((res) => {
          this.detail = res.data.playlist;
          // console.log(this.comment.hotComments[0]);
          // console.log(Boolean(this.comment.hotComments[0]));
        });
      this.axios
        .get("https://apis.netstart.cn/music/comment/playlist", {
          params: {
            id,
          },
        })
        .then((res) => {
          this.comment = res.data;
        });
    },
  },
  created() {
    // this.getPlayListDetail(this.$route.query.id);
    this.id = this.$route.query.id;
  },
  activated() {
    this.id = this.$route.query.id;
  },
  watch: {
    id(a) {
      this.getPlayListDetail(a);
    },
  },
};
</script>

<style lang="less" scoped></style>
