      }"
    ></div>
    <div
      v-else
      class="mask"
      :style="{
        backgroundImage: `url(${currentSong.album.artist.img1v1Url}?imageView=1&type=webp&thumbnail=246x0)`,
      }"
    ></div>
    <div class="title">
      <i class="play-icon" @click="$emit('toggle-show-play-page', false)">
