            fill="#cdcdcd"
            p-id="3742"
          ></path>
        </svg>
      </i>
    </div>
    <!-- 播放器旋转图 -->
    <section class="rotate" v-if="!cutover" @click="cutover = true">
      <img
        class="needle"
        :class="{ paused: !playing }"
        src="https://s3.music.126.net/mobile-new/img/needle-ab.png"
        alt=""
      />
      <section class="record" :class="{ playing: playing }">
        <img
          v-if="!currentSong.album"
          class="thumb"
          :src="currentSong.song ? currentSong.picUrl : currentSong.al.picUrl"
          alt=""
        />
        <img v-else class="thumb" :src="currentSong.album.artist.img1v1Url" alt="" />
        <img class="disc" src="https://s3.music.126.net/mobile-new/img/disc.png" alt="" />
      </section>
    </section>
    <!-- 歌词 -->
    <section class="lyric" v-else>
      <PlayLyrics
        :currentSong="currentSong"
        :currentTime="currentTime"
        :duration="duration"
        :llv="llv"
        :playing="playing"
        @toggle-lyrics="cutover = $event"
      ></PlayLyrics>
    </section>
    <!-- 进度条 -->
    <section class="progress-bar">
      <input
        type="range"
        :max="duration"
        step="0.5"
        v-model="value"
        @change="progressChange"
        @input="progressInput"
      />
      <span class="bar" :style="{ width: (value / duration) * 100 + '%' }"></span>
      <!-- <span class="x" :style="{ width: llv + '%' }"></span>
      <span class="dot" :style="{ left: llv + '%' }"></span> -->
