<script>
import PlayLyrics from "@/components/PlayLyrics.vue";
export default {
  components: {
    PlayLyrics,
  },
  props: {
    currentSong: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    llv: Number,
  },
  data() {
    return {
      cutover: false,
      value: this.currentTime,
      inputing: false,
    };
  },
  watch: {
    currentTime: function(n) {
      if (!this.inputing) {
        this.value = n;
      }
    },
  },
  methods: {
    progressChange: function() {
      this.inputing = false;
      this.$emit("current-time-change", event.target.value);
    },
    progressInput: function() {
      this.inputing = true;
    },
  },
};
</script>

<style lang="less" scoped>
.pos-ab() {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.play-page {
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;

  overflow: hidden;
  &::before {
    content: "";
    display: block;
    .pos-ab();
    z-index: -2;
    background-color: #333;
  }

  .mask {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    filter: blur(30px) brightness(0.8);
    .pos-ab();
    z-index: -1;
    transform: scale(1.5);
    transition: all 0.3s;
  }
  .title {
    width: 100%;
    height: 32px;
    display: flex;
    align-items: center;
    .play-icon {
      width: 22px;
      height: 22px;
      margin-left: 12px;
      z-index: 1;
    }
    p {
      padding-left: 25px;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
      overflow: hidden;
      text-overflow: ellipsis;
      flex: 1;
      color: rgba(255, 255, 255, 0.836);
      margin-left: -34px;
      font-size: 16px;
      span {
        font-size: 14px;
      }
    }
    .share-icon {
      width: 22px;
      height: 22px;
      margin-right: 12px;
      z-index: 1;
    }
  }
  .rotate {
    position: relative;
    padding-top: 25vw;
    img.needle {
      height: 40vw;
      position: absolute;
      top: 0;
      left: 50%;
      z-index: 9;
      margin-left: -15px;
      transform-origin: 15px 15px;
      transform: rotate(0deg);
      transition: all 0.3s;
      &.paused {
        transform: rotate(-20deg);
      }
    }
    .record {
      position: relative;
      width: 80vw;
      height: 80vw;
      margin: 0 auto 0 auto;
      img {
        .pos-ab();
        border-radius: 50%;
        &.thumb {
          transform: scale(0.8);
        }
      }
      animation: rotate 8s linear infinite;
      animation-play-state: paused;
      &.playing {
        animation-play-state: running;
      }
    }
  }
  .lyric {
    // padding-top: 25vw;
    width: 80vw;
    height: 105vw;
    overflow: hidden;
    margin: 0 auto;
  }
  .progress-bar {
    width: 85vw;
    height: 4px;
    margin: 0 auto;
    background-color: lightblue;
    position: relative;
    top: 40px;
    border-radius: 10px;

    input {
      width: 100%;
      outline: none;
      border: none;
      margin: 0;
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      opacity: 0;
      z-index: 9;
    }
    .bar {
      .pos-ab();
      background: lightcoral;
      border-radius: inherit;
      &::before {
        content: "";
        width: 10px;
        height: 10px;
        display: block;
        margin-right: -5px;
        // background: rgba(134, 0, 223, 0.8);
        position: absolute;
        top: -3px;
        right: 0;
        border-radius: 50%;
      }
    }
    // .x {
    //   width: 30%;
    //   height: 4px;
    //   display: block;
    //   background-color: pink;
    // }

    // .dot {
    //   width: 10px;
    //   height: 10px;
    //   // background-color: blue;
    //   display: block;
    //   position: absolute;
    //   bottom: 0;
    //   left: 30%;
    //   margin-left: -5px;
    // }
  }
  .controls {
    padding: 20%;
    display: flex;
    justify-content: space-around;
    color: white;
  }
}
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>
