<template>
  <div class="playlyrics" @click="$emit('toggle-lyrics', false)" ref="lyric">
    <ul class="content" ref="lyricContent" v-if="parsedLyric.length" :style="{ marginTop: -scrollH + 'px' }">
      <li v-for="(l, i) in parsedLyric" :key="i">
        <span
          :style="{
            animationDuration: parsedLyric[i + 1] ? parsedLyric[i + 1].time - l.time - 0.5 + 's' : '3s',
          }"
          :class="{
            active: currentLyricIndex === i,
            playing: playing && currentLyricIndex === i,
          }"
        >
          {{ l.text }}
        </span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    currentSong: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
  },
  data() {
    return {
      value: this.currentTime,
      inputing: false,
      showLyric: false,
      lyric: null,
      lisH: [],
      scrollH: 0,
    };
  },
  watch: {
    currentTime: function(n) {
      if (!this.inputing) {
        this.value = n;
      }
    },

    "currentSong.id": function(id) {
      // console.log(id);
      this.getLyric(id);
    },

    currentLyricIndex: function(index) {
      // var lis = this.$refs.lyricContent
      //   ? this.$refs.lyricContent.querySelectorAll("li")
      //   : null;
      // console.log(index);
      // 当前歌词前面所有的歌词的高度
      var h = this.lisH.slice(0, index).reduce(function(total, item) {
        return total + item;
      }, 0);
      // console.log(h);
      h = h > 100 ? h - 100 : 0;
      this.scrollH = h;
    },
    parsedLyric: function() {
      // console.log("parsedLyric改变", this.$refs.lyricContent);
      this.$nextTick(() => {
        // console.log("nextTick", this.$refs.lyricContent);
        var lis = this.$refs.lyricContent.querySelectorAll("li");
        this.lisH = [...lis].map((item) => item.offsetHeight);
      });
    },
  },
  created: function() {
    this.getLyric(this.currentSong.id);
    // console.log(this.$refs.lyricContent);
  },
  methods: {
    progressChange: function(event) {
      this.inputing = false;
      this.$emit("current-time-change", event.target.value);
    },
    progressInput: function() {
      this.inputing = true;
    },

    getLyric: function(id) {
      this.axios
        .get("https://apis.netstart.cn/music/lyric", {
          params: { id },
        })
        .then((res) => (this.lyric = res.data.lrc.lyric));
    },
  },
  computed: {
    parsedLyric: function() {
      if (this.lyric) {
        return this.lyric
          .split("\n")
          .filter((s) => s)
          .map((s) => {
            var text = s.replace(/^\[\d{2}:\d{2}\.\d{2,3}\]/i, "");
            var timeStr = s.replace(text, "").replace(/(^\[|\]$)/g, "");
            var timeArr = timeStr.split(":").map((item) => Number(item));
            var time = timeArr[0] * 60 + timeArr[1];
            return { text, time };
          });
      } else {
        return [];
      }
    },
    currentLyricIndex: function() {
      var i = this.parsedLyric.findIndex((item) => item.time > this.currentTime);
      var currentLyricIndex = i !== -1 ? i - 1 : this.parsedLyric.length - 1;
      return currentLyricIndex;
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes xxx {
  from {
    background-position-x: 0;
  }
  to {
    background-position-x: -100%;
  }
}
.playlyrics {
  // background-color: #fff;
  overflow: hidden;
  width: 100%;
  // height: 44vh;
  margin-top: 15vh;
  // margin: 0 auto;
  ul {
    margin: 0;
    padding: 0;
    transition: all 0.5s;
    transform: translateY(4vh);
    li {
      font-size: 14px;
      color: rgba(255, 255, 255, 0.8);
      line-height: 25px;
      .active {
        /* cursor: pointer; */
        animation-name: xxx;
        animation-timing-function: linear;
        animation-duration: 3s;
        background-image: linear-gradient(to right, rgba(0, 210, 247, 0.8) 50%, rgba(0, 255, 115, 0.8) 50%);
        background-size: 200%;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        animation-play-state: paused;
        &.playing {
          animation-play-state: running;
        }
      }
    }
  }
}
</style>
